import React, { useEffect } from "react";
import { Form, Icon, Input, Button, Row, Col, Typography, message } from "antd";
import { Link } from "react-router-dom";
import MyModal from "../../util/WarnModal";
import { connect } from "react-redux";
import { validateEmail } from "../../../action/auth";
import { clearErr } from "../../../action/resource";
import { Card } from 'antd';
import useTitle from "../../util/useTitle";
const ValidEmail = props => {
  useTitle("validate email");
  const { err } = props;
  useEffect(() => {
    if (Object.keys(props.err).length) {
      message.error("new props");
      MyModal({ data: props.err });
      props.clearErr();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [err]);

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        props.validateEmail(values);
      }
    });
  };

  const { getFieldDecorator } = props.form;
  return (
    <Row type="flex" justify="center">
      <Col
        style={{
          maxWidth: "740px",
          width: '90%',
          minWidth: "320px",
          margin: "0 auto ",
          padding: "16px",
          borderRadius: "8px",
          // border: "2px solid #e2e2e2",
        }}
      >
        <Card>
          <Form onSubmit={handleSubmit} className="login-form">
            <Typography.Title level={2} type="secondary">
              Email Validation
          </Typography.Title>
            <Form.Item label="Email" >
              {getFieldDecorator("email", {
                rules: [
                  {
                    type: "email",
                    message: "The input is not valid E-mail!"
                  },
                  {
                    required: true,
                    message: "Please input your E-mail!"
                  }
                ]
              })(
                <Input
                  type="email"
                  size="large"
                  prefix={
                    <Icon type="user" style={{ color: "rgba(255,255,255,.25)" }} />
                  }
                  placeholder="The email you registered with"
                />
              )}
            </Form.Item>
            <Form.Item label="Email Validation Code">
              {getFieldDecorator("code", {
                rules: [
                  {
                    required: true,
                    message: "Please input your E-mail!"
                  },

                  {
                    min: 4,
                    max: 4,
                    message: "length of code must be 4 digit"
                  }
                ]
              })(
                <Input
                  size="large"
                  prefix={
                    <Icon type="unlock" style={{ color: "rgba(255,255,255,.25)" }} />
                  }
                  placeholder="validation code"
                />
              )}
            </Form.Item>

            <Form.Item>
              <Link to="/signup">sign up</Link>
              <br />
              <Button
                size="large"
                type="primary"
                htmlType="submit"
                className="login-form-button"
                loading={props.loading}
              >
                Sign in Page
            </Button>
            </Form.Item>
          </Form>
        </Card>
      </Col>
    </Row>
  );
};

const mapStateToProps = state => ({
  auth: state.auth,
  err: state.err,
  loading: state.loading
});

const Signin = Form.create({ name: "signin_form" })(
  connect(
    mapStateToProps,
    { validateEmail, clearErr }
  )(ValidEmail)
);

export default Signin;
